"use client";

import dayjs from "dayjs";
import Link from "next/link";
import Image from "next/image";
import { redirect } from "next/navigation";
import { motion } from "framer-motion";

import {
  getFeedbackByInterviewId,
  getInterviewById,
} from "@/lib/actions/general.action";
import { Button } from "@/components/ui/button";
import { getCurrentUser } from "@/lib/actions/auth.action";

const Feedback = async ({ params }: RouteParams) => {
  const { id } = await params;
  const user = await getCurrentUser();

  const interview = await getInterviewById(id);
  if (!interview) redirect("/");

  const feedback = await getFeedbackByInterviewId({
    interviewId: id,
    userId: user?.id!,
  });

  return (
    <section className="bg-background text-foreground min-h-screen px-6 md:px-20 py-14">
      <motion.div
        initial={{ opacity: 0, y: -10 }}
        animate={{ opacity: 1, y: 0 }}
        className="text-center mb-12"
      >
        <h1 className="text-5xl font-extrabold bg-gradient-to-r from-primary to-cyan-400 bg-clip-text text-transparent">
          Interview Feedback
        </h1>
        <p className="mt-2 text-muted-foreground text-lg">
          Role: <span className="capitalize font-semibold">{interview.role}</span>
        </p>
      </motion.div>

      <div className="grid md:grid-cols-2 gap-6 mb-10">
        <motion.div
          initial={{ opacity: 0, scale: 0.95 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ delay: 0.2 }}
          className="glass-card"
        >
          <div className="flex items-center gap-3">
            <Image src="/star.svg" width={22} height={22} alt="star" />
            <p className="text-lg font-medium">
              Overall Score:
              <span className="text-primary font-bold ml-1">
                {feedback?.totalScore}/100
              </span>
            </p>
          </div>
        </motion.div>

        <motion.div
          initial={{ opacity: 0, scale: 0.95 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ delay: 0.3 }}
          className="glass-card"
        >
          <div className="flex items-center gap-3">
            <Image src="/calendar.svg" width={22} height={22} alt="calendar" />
            <p className="text-lg">
              {feedback?.createdAt
                ? dayjs(feedback.createdAt).format("MMM D, YYYY h:mm A")
                : "N/A"}
            </p>
          </div>
        </motion.div>
      </div>

      {/* Final Assessment */}
      <motion.div
        initial={{ opacity: 0, y: 10 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.4 }}
        className="glass-card mb-8"
      >
        <h2 className="text-xl font-semibold mb-2 text-primary">Final Assessment</h2>
        <p className="text-muted-foreground text-base">{feedback?.finalAssessment}</p>
      </motion.div>

      {/* Breakdown */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.5 }}
        className="mb-10"
      >
        <h2 className="text-2xl font-bold mb-4">📊 Interview Breakdown</h2>
        <div className="space-y-5">
          {feedback?.categoryScores?.map((cat, i) => (
            <div key={i} className="glass-card hover:scale-[1.01] transition">
              <p className="text-lg font-semibold">
                {i + 1}. {cat.name} <span className="text-primary">({cat.score}/100)</span>
              </p>
              <p className="text-muted-foreground">{cat.comment}</p>
            </div>
          ))}
        </div>
      </motion.div>

      {/* Strengths */}
      <div className="mb-8">
        <h3 className="text-xl font-semibold mb-2">💪 Strengths</h3>
        <ul className="list-disc pl-5 space-y-1 text-muted-foreground">
          {feedback?.strengths?.map((point, i) => (
            <li key={i} className="hover:text-foreground transition duration-200">
              {point}
            </li>
          ))}
        </ul>
      </div>

      {/* Areas for Improvement */}
      <div className="mb-12">
        <h3 className="text-xl font-semibold mb-2">🧠 Areas for Improvement</h3>
        <ul className="list-disc pl-5 space-y-1 text-muted-foreground">
          {feedback?.areasForImprovement?.map((area, i) => (
            <li key={i} className="hover:text-foreground transition duration-200">
              {area}
            </li>
          ))}
        </ul>
      </div>

      {/* Buttons */}
      <div className="flex flex-col sm:flex-row gap-4 justify-center">
        <Link href="/" className="flex-1">
          <Button className="w-full hover:scale-105 transition-transform" variant="secondary">
            Back to Dashboard
          </Button>
        </Link>
        <Link href={`/interview/${id}`} className="flex-1">
          <Button className="w-full bg-primary hover:bg-primary/80 hover:scale-105 transition-transform text-black">
            Retake Interview
          </Button>
        </Link>
      </div>
    </section>
  );
};

export default Feedback;
